@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins", sans-serif}body{color:white;background-color:#292929;min-width:260px}

nav{height:80px;display:grid;grid-template-columns:1fr 1fr;-webkit-align-items:center;align-items:center;padding:0 5%;background-color:#292929;border-bottom:1px solid white}nav .logo{text-decoration:none;color:#45a2cb;font-size:2rem}nav .logo .logoFirst{color:orange}nav .logo .authorLogo{color:white;font-size:0.9rem}nav .menu{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;margin-left:auto}nav .menu li{list-style:none}nav .menu li a{text-decoration:none;color:whitesmoke}nav .menu li a:hover{color:#45a2cb}nav .menu li+li{margin-left:4rem}nav .menuBar{display:none}@media screen and (max-width: 960px){nav{grid-template-columns:1fr 0.25fr}nav .menu{display:none}nav .menu li+li{margin:0}nav .active{position:fixed;display:grid;width:100%;height:100vh;background-color:rgba(0,0,0,0.918);left:0;top:0;place-items:center;z-index:100}nav .menuBar{display:block;margin-left:auto;font-size:1.5rem;z-index:100}}

main{display:grid;grid-template-columns:0.5fr 1fr 0.5fr;place-items:center;text-align:center;width:100%;height:calc(100vh - 80px);padding:2rem 0}main .orangeDrink{width:100%}main .blueDrink{width:100%}main .mainWrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:0 5%}main .mainWrapper .mainHeader{text-align:center;margin-bottom:1rem}main .mainWrapper h3+h3{margin-top:1rem}main .mainWrapper .mainBtns{display:-webkit-flex;display:flex;margin:2rem 0}main .mainWrapper .mainBtn{padding:0.5rem 2rem;border:1px solid #eeebeb;border-radius:3rem;outline:none;text-decoration:none;text-transform:uppercase;display:grid;place-items:center}main .mainWrapper .mainBtn:hover{cursor:pointer}main .mainWrapper .alcBtn{background-color:orange;color:black}main .mainWrapper .alcBtn:hover{background-color:#ffb52c}main .mainWrapper .ingrBtn{background-color:#006eff;color:white}main .mainWrapper .ingrBtn:hover{background-color:#328bff}main .mainWrapper .mainBtn+.mainBtn{margin-left:5rem}@media screen and (max-width: 960px){main{grid-template-columns:0.25fr 1.5fr 0.25fr}}@media screen and (max-width: 675px){main{grid-template-columns:1fr}main img{display:none}main .mainWrapper .mainBtns{-webkit-flex-direction:column;flex-direction:column}main .mainWrapper .mainBtn+.mainBtn{margin-left:0;margin-top:1rem}}

.searchWrapper{display:grid;grid-template-columns:0.5fr 1fr 0.5fr;place-items:center;width:100%;height:calc(100vh - 80px);text-align:center}.searchWrapper .alcImg{width:50%}.searchWrapper .alcImg2{width:50%}.searchWrapper .ingImg{width:50%}.searchWrapper .ingImg2{width:50%;-webkit-transform:rotate(-41deg);transform:rotate(-41deg)}.searchWrapper .searchBy{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:relative;-webkit-align-items:center;align-items:center;width:100%;-webkit-justify-content:center;justify-content:center}.searchWrapper .searchBy .selectWrapper{position:relative;width:40%;z-index:10;border:1px solid white;border-radius:3rem;margin:2rem 0}.searchWrapper .searchBy .selectWrapper::after{width:10%;height:100%;display:grid;place-items:center;position:absolute;content:"\25BC";top:0;right:2%;z-index:-1}.searchWrapper .searchBy .selectWrapper .searchSelect{width:100%;border:none;outline:none;-webkit-appearance:none;appearance:none;position:relative;background-color:transparent;color:white;padding:0.5rem 1rem;font-size:1rem}.searchWrapper .searchBy .selectWrapper .searchSelect option{background-color:whitesmoke;color:black}.searchWrapper .searchBy .searchByBtn{padding:0.5rem 3rem;border:1px solid #eeebeb;border-radius:3rem;outline:none;font-size:1rem;background-color:orange;color:black}.searchWrapper .searchBy .searchByBtn:hover{cursor:pointer;background-color:#ffb52c}@media screen and (max-width: 960px){.searchWrapper .searchBy .selectWrapper{width:60%}}@media screen and (max-width: 675px){.searchWrapper{grid-template-columns:1fr}.searchWrapper img{display:none}.searchWrapper .searchBy .selectWrapper{width:60%}}

.drinkPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-row-gap:3rem;row-gap:3rem;grid-column-gap:3rem;-webkit-column-gap:3rem;column-gap:3rem;width:100%;padding:3rem 5%}.drinkPage .drink{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:1px solid whitesmoke;border-radius:1rem;height:auto;background-color:whitesmoke;color:black;transition:all ease 0.3s}.drinkPage .drink:hover{cursor:pointer;-webkit-transform:scale(1.1);transform:scale(1.1)}.drinkPage .drink h3{text-align:center;margin:0.5rem 0}.drinkPage .drink img{width:100%;-webkit-align-items:center;align-items:center;border-top-left-radius:1rem;border-top-right-radius:1rem}@media screen and (max-width: 1200px){.drinkPage{grid-template-columns:1fr 1fr 1fr}}@media screen and (max-width: 960px){.drinkPage{grid-template-columns:1fr 1fr}}@media screen and (max-width: 500px){.drinkPage{grid-template-columns:1fr}}

.drinkDetails{display:grid;grid-template-columns:1fr;place-items:center;margin:3rem 5%;width:90%;position:relative}.drinkDetails .drinkWrapper{background-color:#4b4b4b;display:grid;grid-template-columns:1fr 1fr;width:75%;padding:2rem 0;border-radius:1rem;border:1px solid whitesmoke}.drinkDetails .drinkWrapper .backBtn{position:absolute;top:0;left:0;background:transparent;border:none;outline:none;font-size:3.5rem;color:orange}.drinkDetails .drinkWrapper .backBtn:hover{color:#ffbd43;cursor:pointer}.drinkDetails .drinkWrapper .leftSec{display:grid;place-items:center}.drinkDetails .drinkWrapper .leftSec .drinkImg{width:70%;border-radius:2rem}.drinkDetails .drinkWrapper .rightSec{padding-right:2rem}.drinkDetails .drinkWrapper .rightSec .drinkName{position:relative}.drinkDetails .drinkWrapper .rightSec .drinkName:before{content:"";position:absolute;border-bottom:5px solid white;width:10%;height:100%}.drinkDetails .drinkWrapper .rightSec h3{margin:1rem 0;font-weight:bold;color:orange}.drinkDetails .drinkWrapper .rightSec h4{font-weight:normal}.drinkDetails .drinkWrapper .rightSec .orangy{color:orange}@media screen and (max-width: 960px){.drinkDetails .drinkWrapper{grid-template-columns:1fr;text-align:center}.drinkDetails .drinkWrapper .backBtn{left:-5%}.drinkDetails .drinkWrapper .rightSec{padding-right:0;padding:1rem}.drinkDetails .drinkWrapper .leftSec .drinkImg{width:50%}}

